.landing {
    height: 1200px;
    display: flex;
    flex-flow: column nowrap;
}

.landing-headline {
    height: 50%;
}

.landing-description {
    height: 50%;
    display: flex;
    flex-flow: row nowrap;
    position: relative;
    top: 50px
}

.landing-description-left {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.abstract-box {
    width: 80%;
    position: relative;
    left: 240px;
    bottom: 100px;
}

.abstract-title {
    font-weight: bold;
}

.abstract-text {
    text-align: justify;
}

.landing-description-right {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.links {
    height: 80%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-around;
    align-items: center;
    position: relative;
    right: 50px;
    bottom: 50px;
}

.link-button {
    height: 60px;
    width: 200px;
}

